* {
    font-family: 'Kelly Slab';
    /* font-family: 'Ruslan Display'; */
}
body {
    margin: 0;
    /* background-color: rgb(216, 216, 216); */
}

header {
    height: 40px;
    background-color: rgba(0, 0, 0, 0); 
    width: 100%;
    position: absolute;
}
header::after {
    content: "";
    width: 80%;
    height: 1px;
    display: block;
    position: relative; 
    background-color: rgb(48, 48, 48);
    margin-left: auto;
    margin-right: auto;
}

.logo0 {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.logo {
    background-image: url(../logo/tire-svgrepo-com.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
} 
nav {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 30px;
    align-items: center;
    padding: 0 30px;
    font-family: inherit;
    text-decoration: none;
    /* color: rgb(48, 48, 48); */
    color: whitesmoke;

}
ul {
    margin: 0;
}
ul li {
    min-width: 24px;
}
.menu {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    gap: 20px;
    /* font-family: Andale Mono, monospace; */
    font-weight: bold;
    font-size: 15px;
}
a {
    color: inherit;
    text-decoration: none;
}

section {
    /* width: 100%; */
    height: 100vh;
    background-image: linear-gradient(to bottom right, rgb(40, 163, 40) 30%, white 30.3%,rgb(255, 72, 0) 33%, rgb(0, 42, 119) 80%);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    box-sizing: border-box;
    align-items: center;
    padding-top: 50px;
    letter-spacing: 3px;
    gap: 20px;
    margin: 0;
}

section>a>div {
    width: 200px;
    height: 200px;
    background-color: rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    box-shadow: 0px 1px 2px 0px rgba(139, 139, 139, 0.7);
    transition: transform 1s;
}

section>a>div:hover {
    transform: scale(1.1);
}

section div p {
    /* color: rgb(48, 48, 48); */
    color: whitesmoke;
    font-size: x-large;
    font-family: Andale Mono, monospace;
}
section div div {
    width: 100px;
    height: 100px;
    border-radius: 2px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: center;
    background-position-y: center;
    background-color: rgba(0, 0, 0, 0);
}
.img1 {
    background-image: url(../Main-links/contact-pictures/phone-solid.svg);
}
.img2 {
    background-image: url(../Main-links/contact-pictures/youtube.svg);
    background-size: 100px;
}
.img3 {
    background-image: url(../Main-links/contact-pictures/instagram.svg);
}
.img4 {
    background-image: url(../Main-links/contact-pictures/envelope-solid.svg);
}
.conVal3:hover::after {
    content: "включен VPN?";
    color: crimson;
    text-align: center;
    font-size: medium;
    font-family: Andale Mono, monospace;
}

@media screen and (min-width: 541px) and (max-width: 992px) {
    section {
        padding: 10px 0;
        width: 100vw;
        height: calc(100vh - 40px);
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-template-rows: repeat(2, auto);
        justify-content: center; 
        align-content: center;
        gap: 25px;
    }
    section div {
        width: 250px;
        height: 250px;
    }
    section div div {
        width: 100px;
        height: 100px;
    }
    section div p {
        font-size: large;
    }
}

@media screen and (max-width: 540px){
    header a{
        font-size: 15px;
    }
    section {
        padding-top: 50px;
        width: 100%;
        height: 100vh;
        min-height: 730px;
        /* display: grid;
        grid-template-columns: repeat(2, auto);
        grid-template-rows: repeat(2, auto); */
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-wrap: wrap;
        /* align-content: center; */
        gap: 15px;
        /* margin-left: auto;
        margin-right: auto; */
    }
    section>a>div {
        width: 150px;
        height: 150px;
    }
    section div div {
        width: 60px;
        height: 60px;
    }
    section div p {
        font-size: medium;
    }
    section>a:nth-child(2)>div {
        background-size: contain;
        box-sizing: border-box;
    }
    .img2 {
        background-size: 70px;
    }
    section>a:nth-child(2)>div>div {
        height: 60px;
        width: 100px;
    }
    .menu {
        padding: 0;
    }
    .logo0>div, .menu>li {
        font-size: x-small;
    }
}